import React from 'react';
import Link from "gatsby-link";
import HobbitPreview from '../pages/images/hobbit_preview-500x420.jpg';
import BeorningPreview from '../pages/images/beorning_preview.jpg';
import HumanPreview from '../pages/images/human_preview.jpg';
import ElfPreview from '../pages/images/woodelf_preview.jpg';
import DwarfPreview from '../pages/images/dwarf_preview.jpg';
import UrukPreview from '../pages/images/urukhai_preview.jpg';
import HaradPreview from '../pages/images/haradrim_preview.jpg';
import LhuthPreview from '../pages/images/uruklhuth_preview.jpg';
import OlogPreview from '../pages/images/ologhai_preview.jpg';
import OrcPreview from '../pages/images/orc_preview.jpg';
import RacePreview from "../components/Races/RacePreview";


class RacePage extends React.Component {
    handleRacePreview (title) {
        switch(title)
        {
            case "Hobbits":
                return HobbitPreview;
            case "Humans":
                return HumanPreview;
            case "Wood Elves":
                return ElfPreview;
            case "Dwarves":
                return DwarfPreview;
            case "Beornings":
                return BeorningPreview;
            case "Uruk-Hais":
                return UrukPreview;
            case "Common Orcs":
                return OrcPreview;
            case "Olog-Hais":
                return OlogPreview;
            case "Uruk-Lhuths":
                return LhuthPreview;
            case "Haradrims":
                return HaradPreview;
            default:
                break;
        }
    }

    render() {
        const data = this.props.data;

        return (
            <div className="banner-race">
                <div className="jumbotron races-banner">
                    <div className="container">
                        <h1 className="text-center">Races</h1>
                        <p>Whether as a stalwart defender of Vinyanost, or a fierce guardian of Dol-Guldur, deciding which race to play will define who you'll fight for in this neverending war.</p>
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="col-lg-12">
                                <h2 className="race-people">The Free People</h2>
                            </div>
                            <div className="col-lg-12">
                            {data.allMarkdownRemark.edges.filter(({ node }) => node.frontmatter.raceid < 10)
                            .map(({ node }) =>
                                <RacePreview
                                key = {node.id}
                                title = {node.frontmatter.title}
                                image = {this.handleRacePreview(node.frontmatter.title)}
                                path = {node.frontmatter.path}
                                >
                                {node.excerpt}
                                </RacePreview>
                            )}
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="col-lg-12">
                                <h2 className="race-people">The Forces of the Shadow</h2>
                            </div>
                            <div className="col-lg-12">
                            {data.allMarkdownRemark.edges.filter(({ node }) => node.frontmatter.raceid > 9)
                            .map(({ node }) =>
                                <RacePreview
                                key = {node.id}
                                title = {node.frontmatter.title}
                                image = {this.handleRacePreview(node.frontmatter.title)}
                                path = {node.frontmatter.path}
                                >
                                {node.excerpt}
                                </RacePreview>
                            )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RacePage;



export const query = graphql `
query RacePreview {
    allMarkdownRemark (
        sort: {fields: [frontmatter___raceid], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(races)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                frontmatter {
                    title
                    path
                    raceid
                }
                excerpt(pruneLength: 280)
            }
        }
    }
}
`


