import React from 'react';
import { navigateTo } from 'gatsby-link';
import "./RacePreview.css";

const RacePreview = (props) => {
    return (
        <div key={props.id} className="RacePreview" onClick={() => navigateTo(props.path)}>
            <div className="col-sm-12">
                <div className="row">
                    <div className="col-sm-6">
                        <img src={props.image} className="RacePreview-Img" />
                    </div>
                    <div className="col-sm-6">
                        <div className="RacePreview-Content">
                            <h2 className="RacePreview-Title">{props.title}</h2>
                            <p className="RacePreview-Desc">{props.children}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default RacePreview;


